*, ::after, ::before {
    box-sizing: border-box;
}
body {
    margin: 0;
    height: 100vh;
    max-height: 100%;
    font-family: 'Lato', 'Muli' ,sans-serif;
    font-size: 18px;
    background-color: #F9FCFF;
}
::-webkit-scrollbar-track
{
	border-radius: 10px;
	background-color: rgba(142, 172, 212, 0.1);
}

::-webkit-scrollbar
{
    height: 8px;
	width: 8px;
	background-color: rgba(142, 172, 212, 0.1);
}

::-webkit-scrollbar-thumb
{
	border-radius: 20px;
	background-color: #18214D;
}

ul {
    margin: 0;
    padding: 0;
}
ul li {
    list-style: none;
}
table {
    border: none;
}
h1, h2, h3, h4, h5, p {
    margin: 0;
    padding: 0;
}
p {
    color: #18214D;
    font-size: 16px;
    line-height: 22px;
}
a {
    color: #8EACD5;
    font-size: 16px;
    line-height: 22px;
    transition: all 0.2s ease;
}
a:hover,
a:focus {
    color: #18214D;
}
h1 {
    font-family: 'Muli';
    font-style: normal;
    font-weight: 800;
    font-size: 25px;
    line-height: 33px;
    color: #18214D;
}
h2 {
    font-family: 'Muli';
    font-style: normal;
    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
    color: #18214D;
}

.mb1 {
    margin-bottom: 10px;
}

.container {
    width: 100%;
    max-height: 100%;
    padding: 20px;
    max-width: 1920px;
    margin: 0 auto;
}

.card {
    padding: 40px;
    box-shadow: 5px 5px 15px rgba(24, 33, 77, 0.11);
    border-radius: 20px;
    width: 100%;
    height: 100%;
    max-height: 650px;
    background-color: #fff;
}

.tableHead *,
.tableHead {
    font-family: 'Lato'!important;
    font-style: normal!important;
    font-weight: bold!important;
    font-size: 18px!important;
    line-height: 22px!important;
    color: #18214D!important;
}
.tableBody *,
.tableBody {
    font-family: 'Lato'!important;
    font-style: normal!important;
    font-weight: normal!important;
    font-size: 16px!important;
    line-height: 22px!important;
    color: #18214D!important;
}

.MuiTableCell-stickyHeader {
    background-color: #fff!important;
}

.row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
    padding-bottom: 40px;
}

.cv9,
.cv3 {
    position: relative;
    padding-left: 10px;
    padding-right: 10px;
    width: 100%;
}
.cv9 {
    flex: 0 0 75%;
    max-width: 75%;
}
.cv3 {
    flex: 0 0 25%;
    max-width: 25%;
}


.loadingspinner {
    display: inline-block;
	pointer-events: none;
	width: 20px;
	height: 20px;
	border: 2px solid transparent;
	border-color: #eee;
	border-top-color: #18214D;
	border-radius: 50%;
    -webkit-animation: loadingspin 1s linear infinite;
            animation: loadingspin 1s linear infinite;
    margin-left: 10px;
}

@-webkit-keyframes loadingspin {
	100% {
			transform: rotate(360deg)
	}
}

@keyframes loadingspin {
	100% {
			transform: rotate(360deg)
	}
}


@media screen and (max-width: 1200px) {
    .row {
        flex-direction: column-reverse;
    }
    .cv9,
    .cv3 {
        flex: 0 0 100%;
        max-width: 100%;
    }
    .cv3 {
        padding-bottom: 40px;
    }
    .card {
        padding: 20px;
    }
}


@media screen and (max-width: 767px) {
    .container {
        padding-top: 40px;
    }
}
